<template>
  <div id="app">
    <div class="container" @click="show = false">
      <div class="icon">
        <router-link :to="{ name: 'Home' }">
          <img src="static/logo.png">
        </router-link>
      </div>
      <Navigation :mode="navMode"></Navigation>
    </div>
    <img class="mobile-icon" src="static/menu.png" @click="show = !show">
    <transition name="sidebar">
      <div class="mobile-nav-container" v-if="show">
        <nav @click="show = !show" class="mobile-nav" v-if="show">
          <Navigation></Navigation>
        </nav>
      </div>
    </transition>
    <div id="timeline" @click="show = false">
      <transition name="fade">
        <keep-alive><router-view></router-view></keep-alive>
      </transition>
    </div>
  </div>
</template>

<script>
import Navigation from './components/nav.vue'

export default {
  name: 'app',
  data () {
    return {
      show: false,
      isActive: false,
      navMode: 'horizontal'
    }
  },
  components: {
    Navigation
  }
}
</script>

<style lang="scss">
  .container {
    background-color: rgba(46, 66, 88, .85);
    height: 100px;
    width: 100%;
    box-sizing: border-box;
    z-index: 99;
    position: fixed;
    .icon {
      float: left;
      height: 100%;
      padding: 10px 20px 20px 10px;
      img {
        height: 80%;
      }
    }
    .el-menu {
      float: right;
      height: 100%;
      line-height: 100px;
      background-color: transparent;
      .el-menu-item {
        font-size: 1em;
        height: 100%;
        padding-top: 20px;
      }
      .el-menu-item.is-active {
        color: #bfcbd9;
      }
      .el-menu-item:hover {
        background-color: #48576a;
      }
    }
  }
  .el-carousel {
    overflow: hidden;
    position: relative;
  }
  .mobile-icon {
    display: none;
    position: fixed;
    z-index: 999;
    width: 50px;
    height: 50px;
  }
  .mobile-nav-container {
    position: fixed;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 4;
    background-color: rgba(46, 66, 88, .85);
    .mobile-nav {
      padding: 40px;
      position: relative;
      height: 100%;
      width: 200px;
      padding-top: 100px;
      ul {
        background-color: transparent;
        a {
          text-decoration: none;
          color: #999;
          line-height: 100%;
          li {
            text-align: center;
            height: 100px;
            list-style: none;
            font-size: 2em;
            padding-top: 20px;
          }
        }
      }
    }
  }
  .sidebar-enter-active, .sidebar-leave-active {
    -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .sidebar-enter, .sidebar-leave-active {
    -webkit-transform: translateX(-200px);
    transform: translateX(-200px);
  }
  .fade-enter-active {
    -webkit-transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  @media screen and (min-device-width: 600px) {
    .mobile-nav-div {
      -webkit-transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
  }
  @media screen and (max-device-width: 600px) {
    .mobile-icon {
      display: block;
      margin-left: 20px;
      margin-top: 20px;
    }
    .mobile-nav-div {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      a {
        font-size: 1em;
      }
    }
    .container {
      .icon {
        float: right;
      }
      .el-menu {
        display: none;
      }
    }
    #timeline {
      margin-left: 0;
      text-align: center;
    }
  }
  @media screen and (min-width: 768px) {
    .mobile-nav-div {
      -webkit-transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
  }
  @media screen and (max-width: 768px) {
    .mobile-icon {
      display: block;
    }
    .mobile-nav-div {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      a {
        font-size: 1em;
      }
    }
    .container {
      .icon {
        float: right;
      }
      .el-menu {
        display: none;
      }
    }
    #timeline {
      margin-left: 0;
      text-align: center;
    }
  }
</style>



