<template>
	<div class="timeline">
    <div class="pickers">
      <div class="selector type-selector">
        <el-select v-model="typeValue" @change="changeType" clearable placeholder="选择类型">
          <el-option
            v-for="type in types"
            :key="type"
            :label="type"
            :value="type">
          </el-option>
        </el-select>
      </div>
      <div class="date-selector">
        <el-date-picker
          v-model="monthValue"
          type="month"
          placeholder="选择时间"
          :picker-options="disableDays"
          @change="changeTime">
        </el-date-picker>
      </div>
    </div>
    <div class="cards">
      <el-card :body-style="{ padding: '0px' }" v-for="video in videos" :key="video.video_id">
        <div slot="header" class="clearfix">
          <h3>{{ video.time + " " + video.name }}</h3>
          <div class="video-buttons" v-for="button in video.buttons">
            <a :href="button.link" target="_blank">
              <el-button style="float: right;" type="text">{{ button.name }}</el-button>
            </a>
          </div>
        </div>
        <div class="block">
          <img :src="video.cover" class="image">
        </div>
      </el-card>
    </div>
	</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      monthValue: '',
      disableDays: {
        disabledDate (time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2016/1/31 23:59:59').getTime()
        }
      },
      types: ['中字', '饭拍'],
      typeValue: '',
      videos: [],
      videosBuffer: []
    }
  },
  mounted () {
    axios.get('static/api/videos.json').then(response => {
      var videos = response.data.videos
      for (var i = 0; i < videos.length; i++) {
        if (videos[i].type === '中字') {
          videos[i].name = videos[i].name + '【' + videos[i].type + '】'
        }
      }
      this.videos = videos
      this.videosBuffer.push(videos)
    })
  },
  methods: {
    changeTime () {
      if (this.videosBuffer.length > 0) {
        if (this.monthValue) {
          // 需要考虑年份
          var videos = this.videosBuffer[this.videosBuffer.length - 1]
          var year = (this.monthValue.getYear() + '').substring(1, 3)
          var month = this.monthValue.getMonth() + 1 + ''
          var vidToRender = []
          // select corresponding videos from existing items
          for (var i = 0; i < videos.length; i++) {
            if ((videos[i].time.substring(3, 4) === month) && videos[i].time.substring(0, 2) === year) {
              vidToRender.push(videos[i])
            }
          }
          this.videosBuffer.push(vidToRender)
          this.videos = vidToRender
        } else {
          // reset the videos
          this.videosBuffer.pop()
          this.videos = this.videosBuffer[this.videosBuffer.length - 1]
        }
      }
    },
    changeType () {
      if (this.typeValue) {
        var vid = this.videosBuffer[this.videosBuffer.length - 1]
        var vidToRender = []
        for (var i = 0; i < vid.length; i++) {
          if (vid[i].type === this.typeValue) {
            vidToRender.push(vid[i])
          }
        }
        this.videosBuffer.push(vidToRender)
        this.videos = vidToRender
      } else {
        // reset the videos
        this.videosBuffer.pop()
        this.videos = this.videosBuffer[this.videosBuffer.length - 1]
      }
    }
  }
}
</script>

<style scpoed lang="scss">
  @import "./scss/timeline.scss";
</style>



