<template>
  <div class="timeline">
    <div class="blank"></div>
    <div class="pickers">
      <div class="selector site-selector">
        <el-cascader
        :options="sitesForSelector"
        expand-trigger="hover"
        filterable
        clearable
        placeholder="选择站子与资源类型"
        v-model="siteValue"
        @change="changeSite">
        </el-cascader>
      </div>
      <div class="selector date-selector">
        <el-date-picker
          v-model="monthValue"
          type="month"
          placeholder="选择时间"
          :picker-options="disableDays"
          @change="changeTime">
        </el-date-picker>
      </div>
    </div>
    <div class="cards">
      <el-card :body-style="{ padding: '2px' }" v-for="activity in activities" :key="activity.activity_id">
        <div slot="header" class="clearfix">
          <h3>{{ activity.time + " " + activity.name }}</h3>
          <router-link :to="{ name: 'ActivityDetails', params: {id: activity.activity_id} }">
            <el-button style="float: right;" type="text">更多</el-button>
          </router-link>
        </div>
        <div class="block">
          <el-carousel trigger="click">
            <el-carousel-item v-for="pic in activity.pics">
              <img :src="pic" class="image">
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      sitesForSelector: [],
      siteValue: [],
      types: ['高清', '预览'],
      typeValue: '',
      monthValue: '',
      disableDays: {
        disabledDate (time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2016/1/31 23:59:59').getTime()
        }
      },
      activities: [],
      activitiesBuffer: [],
      photos: []
    }
  },
  mounted () {
    axios.all([
      axios.get('static/api/activities.json'),
      axios.get('static/api/photos.json'),
      axios.get('static/api/sitesForSelector.json')
    ]).then(axios.spread((act, pho, sit) => {
      var photos = pho.data.photos
      this.sitesForSelector = sit.data.sitesForSelector
      this.photos = photos
      var activities = act.data.activities
      for (var i = 0; i < activities.length; i++) {
        for (var j = 0; j < photos.length; j++) {
          if (activities[i].activity_id === photos[j].activity_id) {
            for (var k = 0; k < photos[j].pics.length; k++) {
              if (photos[j].pics[k].type === '高清') {
                if (photos[j].pics[k].urls.length <= 3) {
                  activities[i].pics = photos[j].pics[k].urls
                } else {
                  activities[i].pics = photos[j].pics[k].urls.slice(0, 3)
                }
                break
              }
            }
          }
        }
      }
      this.activities = activities
      this.activitiesBuffer.push(activities)
    }))
  },
  beforeRouteEnter (to, from, next) {
    console.log('timeline')
    next(true)
  },
  methods: {
    changeTime () {
      if (this.activitiesBuffer.length > 0) {
        if (this.monthValue) {
          // 需要考虑年份
          var activities = this.activitiesBuffer[this.activitiesBuffer.length - 1]
          var year = (this.monthValue.getYear() + '').substring(1, 3)
          var month = this.monthValue.getMonth() + 1 + ''
          var actToRender = []
          // select corresponding activities from existing items
          for (var i = 0; i < activities.length; i++) {
            if ((activities[i].time.substring(3, 4) === month) && activities[i].time.substring(0, 2) === year) {
              actToRender.push(activities[i])
            }
          }
          this.activitiesBuffer.push(actToRender)
          this.activities = this.activitiesBuffer[this.activitiesBuffer.length - 1]
        } else {
          // reset the activities
          this.activitiesBuffer.pop()
          this.activities = this.activitiesBuffer[this.activitiesBuffer.length - 1]
        }
      }
    },
    changeSite () {
      if (this.activitiesBuffer.length > 0) {
        if (this.siteValue.length > 0) {
          axios.get('static/api/sites.json').then(response => {
            var sites = response.data.sites
            var siteValue = this.siteValue
            var actIDToRender = []
            var actToRender = []
            var activities = this.activitiesBuffer[this.activitiesBuffer.length - 1]
            for (var i = 0; i < sites.length; i++) {
              if (sites[i].name === siteValue[0]) {
                for (var j = 0; j < sites[i].acts.length; j++) {
                  if (sites[i].acts[j].type === siteValue[1]) {
                    actIDToRender.push(sites[i].acts[j].activity_id)
                  }
                }
              }
            }
            for (var m = 0; m < activities.length; m++) {
              for (var k = 0; k < activities.length; k++) {
                if (activities[k].activity_id === actIDToRender[m]) {
                  actToRender.push(activities[k])
                }
              }
            }
            this.activitiesBuffer.push(actToRender)
            this.activities = actToRender
          })
        } else {
          this.activitiesBuffer.pop()
          this.activities = this.activitiesBuffer[this.activitiesBuffer.length - 1]
        }
      }
    }
  }
}
</script>

<style scpoed lang="scss">
  @import "./scss/timeline.scss";
</style>



