// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import AboutBona from './components/aboutBona.vue'
import Photos from './components/photos.vue'
import Videos from './components/videos.vue'
import Home from './components/home.vue'
import Links from './components/links.vue'
import UpdateLog from './components/updateLog.vue'
import SNS from './components/sns.vue'
import ActivityDetails from './components/activity-details.vue'
import SNSDetails from './components/sns-details.vue'
import Admin from './components/admin.vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

Vue.use(VueRouter)
Vue.use(ElementUI)

const routes = [
  { path: '/photos', name: 'Photos', component: Photos },
  { path: '/videos', name: 'Videos', component: Videos },
  { path: '/aboutBona', name: 'AboutBona', component: AboutBona },
  { path: '/home', name: 'Home', component: Home },
  { path: '/links', name: 'Links', component: Links },
  { path: '/updateLog', name: 'UpdateLog', component: UpdateLog },
  { path: '/sns', name: 'SNS', component: SNS },
  { path: '/activitydetails/:id', name: 'ActivityDetails', component: ActivityDetails },
  { path: '/snsdetails/:id', name: 'SNSDetails', component: SNSDetails },
  { path: '/admin', name: 'Admin', component: Admin },
  { path: '/', redirect: '/home' }
]

const router = new VueRouter({
  routes
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



