<template>
  <div class="activity-details">
    <h3>日期+活动</h3>
    <el-collapse v-model="activeNames" v-for="pic in pics">
      <el-collapse-item :title="pic.site_name" :name="pic.id">
        <img :src="p" v-for="p in pic.urls">
        <el-button style="float: right;" type="text" @click="closeCollapse(pic.id)">收起</el-button>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pics: [
        {
          site_name: 'kaleidoscope_BN',
          id: '1',
          urls: [
            'http://cfile4.uf.tistory.com/original/2707344D590F221D0EE69F',
            'http://cfile22.uf.tistory.com/original/2749264D590F22263A8B8D'
          ]
        },
        {
          site_name: 'BONAPICS0819',
          id: '2',
          urls: [
            'http://wx3.sinaimg.cn/mw690/006jshhSgy1ffcq4t5k6ej312w1mcdx2.jpg',
            'http://wx4.sinaimg.cn/mw690/006jshhSgy1ffcq4qd8uvj312w1mcas1.jpg',
            'http://wx1.sinaimg.cn/mw690/006jshhSgy1ffcq4nmurij312w1mck8v.jpg'
          ]
        },
        {
          site_name: 'SD_BONA',
          id: '3',
          urls: [
            'http://wx2.sinaimg.cn/mw690/006jshhSgy1ffck37ubfnj30xc0m8tbn.jpg',
            'http://wx2.sinaimg.cn/mw690/006jshhSgy1ffck37uu8mj30m80xc41j.jpg'
          ]
        }
      ],
      activeNames: []
    }
  },
  methods: {
    closeCollapse (id) {
      console.log(id)
      for (var i = 0; i < this.activeNames.length; i++) {
        if (this.activeNames[i] === id) {
          this.activeNames.splice(i, 1)
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .activity-details {
    margin-left: 20px;
    padding-top: 100px;
    width: 95%;
    .el-collapse {
      img {
        width: 100%;
      }
    }
  }
  @media screen and (max-device-width: 768px) {
    .activity-details {
      .el-collapse {
        text-align: left;
      }
    }
  }
</style>



